import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {PostService} from '../post/post.service';
import {Post} from '../post/post.model';
import {AppComponent} from '../app.component';

@Component({
    moduleId: module.id,
    selector: 'category',
    templateUrl: 'category.component.html'
})
export class CategoryComponent extends AppComponent implements OnInit {
    private posts: Post[];
    private isLoading = false;
    param = {
        skip: 0,
        limit: 9,
        category: ''
    }

    constructor(private _route : ActivatedRoute, private _postService: PostService) { 
        super();
    }

    ngOnInit() {
        this._route.params.forEach(params => {
            this.isLoading = false;
            let slug = params["category"];
            this.param.category = slug;
            this._postService.getPosts(this.param)
            .subscribe((posts: Post[]) => {
                this.posts = posts;
                this.isLoading = true;
            })
        })
    }
}


